:root {
    --color-light-border-link: #007aff;
    --color-light-border-secondary-inverted: #b6bcc3;
    --color-light-graphic-accent: #ef3124;
    --color-light-graphic-neutral: #dbdee1;
    --color-light-graphic-primary-inverted: #fff;
    --color-light-graphic-secondary: #6d7986;
    --color-light-text-primary: #0b1f35;
    --color-light-text-secondary: #546272;
}
:root {

    /* Hard */

    /* Up */

    /* Hard up */
}
:root {
    --gap-3xs: 2px;
    --gap-2xs: 4px;
    --gap-s: 12px;
    --gap-m: 16px;
}
:root {
    --border-radius-xl: 16px;
    --border-radius-circle: 50%;
}
:root {
    --focus-color: var(--color-light-border-link);
    --disabled-cursor: not-allowed;
}
:root {
    --switch-label-color: var(--color-light-text-primary);
    --switch-hint-color: var(--color-light-text-secondary);
    --switch-bg-color: var(--color-light-graphic-secondary);
    --switch-border-color: var(--color-light-graphic-secondary);

    /* checked */
    --switch-checked-bg-color: var(--color-light-graphic-accent);
    --switch-checked-border-color: var(--color-light-graphic-accent);

    /* disabled */
    --switch-disabled-color: var(--color-light-text-secondary);
    --switch-disabled-bg-color: var(--color-light-graphic-neutral);
    --switch-disabled-border-color: var(--color-light-graphic-neutral);

    /* inactive */
    --switch-inactive-color: var(--color-light-border-secondary-inverted);
}
.switch__component_1mijv {
    display: inline-flex;
    align-items: flex-start;
    margin: 0;
    padding: 0;
    border: 0;
    cursor: pointer;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.switch__component_1mijv input {
    opacity: 0;
    position: absolute;
}
.switch__start_1mijv {
    align-items: flex-start;
}
.switch__center_1mijv {
    align-items: center;
}
.switch__addons_1mijv {
    margin-left: auto;
    padding-left: var(--gap-m);
    line-height: 24px;
}
.switch__block_1mijv {
    width: 100%;
}
.switch__switch_1mijv {
    position: relative;
    border-radius: var(--border-radius-xl);
    width: 36px;
    height: 20px;
    margin: var(--gap-3xs);
    flex-shrink: 0;
    background-color: var(--switch-bg-color);
    border: 2px solid var(--switch-border-color);
    transition: background-color 0.2s ease, border-color 0.2s ease;
    box-sizing: border-box;
}
.switch__switch_1mijv:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 16px;
    height: 16px;
    border-radius: var(--border-radius-circle);
    background-color: var(--color-light-graphic-primary-inverted);
    box-sizing: border-box;
    transition: transform 0.2s ease;
}
.switch__content_1mijv {
    margin-left: var(--gap-s);
    flex-grow: 1;
}
.switch__label_1mijv {
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    display: block;
    color: var(--switch-label-color);
}
.switch__label_1mijv:not(:only-child) {
    margin-bottom: var(--gap-2xs);
}
.switch__hint_1mijv {
    font-size: 14px;
    line-height: 18px;
    font-weight: 400;
    display: block;
    color: var(--switch-hint-color);
}
/* Reversed state */
.switch__component_1mijv.switch__reversed_1mijv {
    flex-direction: row-reverse;
}
.switch__reversed_1mijv .switch__content_1mijv {
    margin-right: var(--gap-m);
    margin-left: 0;
}
.switch__reversed_1mijv .switch__addons_1mijv {
    margin-left: 0;
    padding-left: 0;
    margin-right: auto;
    padding-right: var(--gap-m);
}
/* Checked state */
.switch__checked_1mijv .switch__switch_1mijv {
    background-color: var(--switch-checked-bg-color);
    border-color: var(--switch-checked-border-color);
}
.switch__checked_1mijv .switch__switch_1mijv:before {
    /* ширина компонента(36px + 2*2px) - отступы(2 * 2px) - размер кружка(20px) */
    transform: translateX(16px);
}
/* Disabled state */
.switch__disabled_1mijv {
    cursor: var(--disabled-cursor);
}
.switch__disabled_1mijv .switch__switch_1mijv {
    background-color: var(--switch-disabled-bg-color);
    border-color: var(--switch-disabled-border-color);
}
.switch__disabled_1mijv .switch__label_1mijv {
    color: var(--switch-disabled-color);
}
.switch__disabled_1mijv .switch__hint_1mijv {
    color: var(--switch-disabled-color);
}
/* Inactive */
.switch__inactive_1mijv {
    cursor: var(--disabled-cursor);
}
.switch__inactive_1mijv .switch__switch_1mijv {
    background-color: var(--switch-disabled-bg-color);
    border-color: var(--switch-disabled-border-color)
}
.switch__inactive_1mijv .switch__switch_1mijv:before {
        background-color: var(--switch-inactive-color);
    }
/* Focused state */
.switch__focused_1mijv .switch__switch_1mijv {
    outline: 2px solid var(--focus-color);
    outline-offset: 2px;
}
